import React, {useState} from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Stack from 'react-bootstrap/Stack';
import DatePicker from "react-datepicker";

import "react-datepicker/dist/react-datepicker.css";

const fromAndTo = [
    {caption: 'Москва', value: '1'},
    {caption: 'Berlin', value: '2'},
    {caption: 'Rome', value: '3'},
];

const aviaCompanies = [
    {caption: 'Aeroflot', value: '1'},
    {caption: 'S7', value: '2'},
    {caption: 'Ural-Airlines', value: '3'},
]

function App() {
    const [startDate, setStartDate] = useState<Date | null>(null);
    const [endDate, setEndDate] = useState<Date | null>(null);
    return (
        <div className="App">
            <Container className="containerExtra">
                <h1>Flight finder</h1>
                <Stack gap={3} className="finderControlsRow">
                    <Row>
                        <Col xs={3}>
                            <Form.Select>
                                <option className='selectPlaceHolder'>ОТКУДА</option>
                                {fromAndTo.map((el) => (
                                    <option value={el.value}>{el.caption}</option>
                                ))}
                            </Form.Select>
                        </Col>
                        <Col xs={3}>
                            <Form.Select>
                                <option className={'selectPlaceHolder'}>КУДА</option>
                                {fromAndTo.map((el) => (
                                    <option value={el.value}>{el.caption}</option>
                                ))}
                            </Form.Select>
                        </Col>
                        <Col xs={2}>
                            <Row>
                                <DatePicker
                                    selected={startDate}
                                    onChange={(date) => setStartDate(date)}
                                    className="datePickerExtra"
                                    placeholderText={'КОГДА'}
                                />
                            </Row>
                        </Col>
                        <Col xs={2}>
                            <Row>
                                <DatePicker
                                    selected={endDate}
                                    onChange={(date) => setEndDate(date)}
                                    className="datePickerExtra"
                                    placeholderText={'ОБРАТНО'}
                                />
                            </Row>
                        </Col>
                        <Col xs={2}>
                            <Form.Select>
                                <option className={'selectPlaceHolder'}>Авиакомпания</option>
                                {aviaCompanies.map((el) => (
                                    <option value={el.value}>{el.caption}</option>
                                ))}
                            </Form.Select>
                        </Col>
                    </Row>
                    <Row className='buttonRow'>
                        <Col>
                            <Button>Поиск</Button>
                        </Col>
                    </Row>
                </Stack>
            </Container>
        </div>
    );
}

export default App;
